    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

html { scroll-behavior: smooth; } 
body { background-image: url(/static/media/topography.fb9a7da3.webp); background-image: var(--subtle-pattern);}

#innerContent {
    position: relative;
    height: auto;
}

.container  {
    width: 100%;
    max-width: 960px;
    margin: auto;
}

.site-content.shift{
    transition: all 1s ;
}
.site-content{
    width: calc( 100% - 80px);
    -webkit-transform: translateX(80px);
            transform: translateX(80px);
}

.site-content .section {
    padding: 6rem 0;
}

.headline {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 1rem;
}
.subhead {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
}

.body-copy{
    font-size: 1rem;
    line-height: 1.6rem;
