/* InnerContent Navigation */

.site-nav{
    position: absolute;
    top: 0; left: 0;
    height: 100vh;
    width: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 20px;
    -webkit-align-items: center;
            align-items: center;
    transition: all 0.5s ease-in-out;
    opacity: 0;
}

.site-nav.sticky{
    position: fixed;
    z-index: 100;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    opacity: 1;
}

.site-nav .site-branding{
    padding: 20px;
}
.site-nav ul {
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-row-gap: 30px;
    row-gap: 30px;
    -webkit-justify-content: center;
            justify-content: center;
    height: 100%;
}
.site-nav ul a {
    padding: 0;
    color: #000;
    text-decoration: none;
    text-align: center;
    position: relative;
}
.site-nav ul a .hover-icon, .site-nav ul a .icon-text {
    display: block;
}

.site-nav ul a .icon-text {
    background-color: #2b6777;
    background-color: var(--primary-color);
    color: #fff;
    padding: 2px 5px;
    font-size: 10px;
    border-radius: 20px;
    margin: 10px 0 0 0;
    position: relative;
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    opacity: 0;
    transition: all 0.5s ease-in-out;
}
.site-nav ul a .icon-text::before {
    content: "";
    position: absolute;
    top: -7px;
    left: 50%;
    width: 0;
    border-bottom: 10px solid #2b6777;
    border-bottom: 10px solid var(--primary-color);
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.site-nav ul a:hover .icon-text {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}


.nav__list .site-branding{
    font-size: 30px;
    line-height: 25px;
    font-weight: bold;
    margin-top: -23px;
    margin-left: 15px;
    color: #2b6777;
    color: var(--primary-color);
}


@media (max-width: 767px) {
    .App {
        padding: 15px;
    }
    .home-nav.sticky{
        position: fixed;
        width: 100%;
        background-color: #FFF;
        z-index: 10;
        top: 0; left: 0;
        box-shadow: rgb(0 0 0 / 15%) 1.95px 1.95px 2.6px;
    }

    .site-nav{ display: none;}

    .site-content {
        width: 90%;
        -webkit-transform: translateX(0);
                transform: translateX(0);
        margin: auto;
    }
    
}

@media (min-width: 768px) and (max-width: 1023px) {
    .site-content {
        width: 90%;
        -webkit-transform: translateX(0);
                transform: translateX(0);
        margin: 0 auto;
    }
    .home-nav.sticky{
        position: fixed;
        width: 100%;
        margin-left: -40px;
        background-color: #FFF;
        z-index: 10;
        top: 0;
    }

    .site-nav{ display: none;}
}

@media (min-width: 1024px) {
    .nav__list .site-branding{
        display: none;
    }
}
    
/* Page: About */

#about {
    height: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: left;
}

.about-content{
    display: -webkit-flex;
    display: flex;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
}
.about-content img{
    -webkit-align-self: flex-start;
            align-self: flex-start;
}

.myskills {
    display: grid;
    grid-template-columns: 33.3% 33.3% 33.3%;
    list-style: none;
    line-height: 1.2rem;
}

.myskills li {
    margin-bottom: 10px;
    font-size: 1rem;
}

@media (max-width: 600px) {
    .about-content{
        -webkit-flex-direction: column-reverse;
                flex-direction: column-reverse;
    }
    .about-content img{
        width: 100%; margin-bottom: 20px;
    }
    .myskills {
        display: grid;
        grid-template-columns: 50% 50%;
    }
}
#home {
