}
#home .nav__list a .icon-text{
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    display: block;
}
#home .nav__list a .icon-text::before {
    content: "";
    position: absolute;
    top: -8px;
    left: 50%;
    width: 0;
    border-bottom: 10px solid var(--primary-color);
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
#home .nav__list a:hover .icon-text{
   opacity: 1;-webkit-transform: translateY(8px);transform: translateY(8px);
}
#home .nav__list a .icon-text{
    display: block;
}


.scroll__down{
    position:absolute;
    bottom:2.5rem;
    left: 0;
    width: 100%;
  }
.scroll__down a{
    text-decoration: none;
  }
  
  .home__scroll-name{
    font-size: 12px; text-decoration: none; color: var(--primary-color);
  }

  
  
  .mouse{
    border: 2px solid var(--primary-color);
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
  }
  
  .wheel{
    background-color: var(--primary-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    -webkit-animation: ani-mouse 2s linear infinite;
            animation: ani-mouse 2s linear infinite;
  }
  
  @-webkit-keyframes ani-mouse {
    0%{
     top: 29%;
    }
    15%{
     top: 50%;
    }
    50%{
     top: 50%;
    }
    100%{
     top: 29%;
    }
   
  }
  
  @keyframes ani-mouse {
    0%{
     top: 29%;
    }
    15%{
     top: 50%;
    }
    50%{
     top: 50%;
    }
    100%{
     top: 29%;
    }
   
  }





@media (max-width: 600px) {

